.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 210px;
    background-color: black;
    .footer_logo {
        padding-bottom: 20px;
        @media screen and (max-width: 768px) {
            width: 25%;
        }
    }
    img {
        @media screen and (max-width: 768px) {
            width: 60%;
        }
    }
}